html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
body {
  background-color: #fbf9fe;
}

.wrap{
  position: relative;
}
h3{
  margin: 34% auto;
  text-align: center;
}
.btn{
  width: 40%;
  height: 2.5rem;
  position: absolute;
  bottom: 15%;
  left: 30%;
  background: transparent;
  text-indent:-9999px;
  border:none;
}
.bg{
  height: -webkit-fill-available;
  overflow: hidden;
  width: 100%;
}

.questionWrap{
  padding:0.2rem 0.3rem 0;
}
.btnBox{
  overflow: hidden;
  margin: 0 -0.15rem 0.15rem;
}
.prev, .next{
  background: #fff;
  border: none;
  float: left;
  font-size: 0.3rem;
}
.next{
  float: right;
}
.icon{
  vertical-align: sub;
}
.title{
  font-size: 0.35rem;
  margin-left: -0.15rem;
  padding: 0.05rem 0 0.1rem;
}
.question{
  font-size:0.4rem;
  line-height: 0.6rem;
}
.answer {
  display: block !important;
  margin-top:0.4rem;
  font-size:0.4rem;
  height: 0.6rem;
  line-height: 0.6rem;
  padding-left: 0.3rem;
  border-radius: 0.03rem;
  border: 0.01rem solid #ccc;
  background-color: #fff;
}
.answer-item-selected {
  border-color: #bd9111;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 98% 65%;
  background-size: 7%;
}

.vux-checker-item {
  display: inline-block;
}

.vux-tap-active {
  tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.vux-tap-active:active {
  background-color: #ECECEC;
}

h5{
  font-size:30px;
  margin-top:3%;
  margin-left:5%;
}
.fraction{
  font-size:50px;
  text-align: center;
  margin-top:8%;
}
.resStartAnswer{
  border: none;
  background: #b7dcdc;
  font-size: 18px;
  width: 40%;
  margin-left: 30%;
  margin-top: 40px;
  padding: 8px;
  border-radius: 5px;
}
